.contener {
  max-width: 680px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.wrapper40px {
  padding-top: 40px;
  padding-bottom: 40px;
}

.wrapper__title, .wrapper__title-small {
  padding-bottom: 32px;
}

.wrapper__title-tiny {
  padding-bottom: 12px;
}

p {
  padding-bottom: 16px;
}

.wrapper__image {
  border-radius: 4px;
  margin-top: 16px;
  display: flex;
  overflow: hidden;
}

.wrapper__image img {
  width: 100%;
}

.header {
  padding-top: 80px;
}

.header h1 {
  letter-spacing: -.05em;
  font-size: 4rem;
  font-weight: 550;
  line-height: 95%;
}

.header h1 span {
  color: #ffb800;
  display: block;
}

@media (width <= 680px) {
  .header h1 {
    font-size: 10vw;
  }
}

.desktop__content {
  border-radius: 4px;
  flex-direction: column;
  gap: 0;
  display: flex;
  overflow: hidden;
}

.projectinfo__content {
  gap: 12px;
  display: flex;
}

.projectinfo__item {
  background-color: #ffffff08;
  border-radius: 4px;
  padding: 16px 18px;
  font-size: .875rem;
}

@media (width <= 680px) {
  .projectinfo__content {
    flex-direction: column;
  }
}

.list__item {
  position: relative;
}

.list__number {
  font-size: .875rem;
  position: absolute;
  left: -24px;
}

@media (width <= 760px) {
  .list__item {
    gap: 24px;
    display: flex;
  }

  .list__number {
    position: static;
  }
}

.wrapper__questions {
  flex-wrap: wrap;
  gap: 12px;
  display: flex;
}

.wrapper__questions .question__item {
  max-width: 218px;
  width: 100%;
  background-color: #ffffff08;
  border-radius: 4px;
  flex-direction: column;
  gap: 32px;
  padding: 16px 19px;
  display: flex;
  overflow: hidden;
}

@media (width <= 480px) {
  .wrapper__questions .question__item {
    max-width: none;
  }
}

